.end {
  display: flex;
  align-items: center;
  height: 42px;

  &__test-block {
    position: relative;
    padding-left: 17px;
    margin-left: 19px;
  }

  &__test-block::before {
    content: "";
    position: absolute;
    height: 42px;
    width: 1px;
    background-color: #fff;
    opacity: 0.1;
    left: 0;
    top: calc(50% - 21px);
  }

  &__text1 {
    font-size: 12px;
    color: #a0a9b2;
  }

  &__text2 {
    font-size: 14px;
    color: #fff;
  }
}
